@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body,
#root,
input {
  margin: 0;
  padding: 0;
  background-color: #eff5f5;
  /* height: 100%; */
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  font-size: 1.6rem;
}

/* Main Page */
.mainpage__container {
  display: grid;
  grid-template-areas:
    'header'
    'main';

  grid-template-rows: 6rem 1fr;
  grid-template-columns: 1fr;
  height: 100%;
}
.mainpage__header {
  background-color: #e6e6e6;
  grid-area: header;
  font-size: 2rem;
  padding: 1rem;
  display: flex;
  align-items: center;
}
.mainpage__header a {
  text-decoration: none;
  color: black;
}
.mainpage__main {
  grid-area: main;
}
.mainpage__main--content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 6rem;
  left: 0;
}
.mainpage__main--title {
  font-size: 8rem;
  text-align: center;
  color: #384558;
}
.mainpage__main--title p {
  margin: 0;
}
.mainpage__main--title p:last-child {
  font-size: 1.5rem;
  font-weight: 500;
  color: #999;
  margin-bottom: 3rem;
}
.mainpage__main--title span {
  font-weight: bolder;
}
.mainpage__main div input {
  width: 50rem;
  height: 4rem;
  border-style: none;
  background-color: transparent;
  border-bottom: 0.2rem solid #999;
  font-size: 2rem;
}

.mainpage__main div input:focus {
  outline: none;
}

.mainpage__footer {
  background-color: #e6e6e6;
  font-size: 1.6rem;
  display: flex;
  padding: 2rem;
  justify-content: center;
  align-items: center;
  margin-top: 57vh;
}

/* Todolist */
.todolist__container {
  margin-top: 2rem;
  font-size: 2rem;
}
.todolist {
  background-color: #dfecec;
  min-width: 47rem;
  padding: 1rem;
  border-radius: 0.5rem;
  margin: 1rem;
  height: 2rem;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.todolist__content {
  display: inline-block;
  cursor: pointer;
}
.todolist div i {
  padding: 0 0.5rem;
}

.fa-trash {
  color: red;
  cursor: pointer;
  transition: all 0.3s;
}
.fa-trash:hover {
  color: #cc0000;
  transform: scale(1.1);
}
.fa-pen {
  margin-left: 1rem;
  color: #00dbdb;
  cursor: pointer;
  transition: all 0.3s;
}
.fa-pen:hover {
  color: #00b3b3;
  transform: scale(1.1);
}
.check-style {
  color: hsl(0, 0%, 75%);
  background-color: #eff5f5;
  text-decoration: line-through;
}

@media (max-width: 768px) {
  html {
    font-size: 50%;
  }
}

